function Demo() {
  const [highlights, setHighlights] = React.useState([...demo_1])
  const [v, setV] = React.useState([])
  const [canAdd, setCanAdd] = React.useState(false)
  const [c, setC] = React.useState(new Date().getTime())
  const [url, setUrl] = React.useState("/demo_1.pdf")
  const [showH, setShowH] = React.useState(true)

  let _colorIncorrect = "#ff3838"
  let _colorIncorrectLight = "#ff383833"
  let _colorCorrect = "#1cad1a"
  let _colorCorrectLight = "#38ff4733"
  let _colorNull = "#c1c1c1"
  let _colorNullLight = "#c1c1c133"

  console.log("render:", highlights)
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100vh",
        width: "100vw",
        fontFamily: "Arial, sans-serif",
        // border: "1px solid #dddddd",
        borderRadius: "8px",
        overflow: "hidden",
      }}
    >
      {/* Header */}
      <div
        style={{
          height: "60px",
          backgroundColor: "#1e90ff",
          color: "white",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          fontSize: "20px",
          fontWeight: "bold",
        }}
      >
        PDF Viewer
      </div>

      {/* Main content */}
      <div style={{ display: "flex", flex: 1 }}>
        {/* Sidebar */}
        <div
          style={{
            width: "200px",
            backgroundColor: "#f4f4f4",
            padding: "16px",
            borderRight: "1px solid #ddd",
          }}
        >
          {/* <div>List fields</div> */}
          <div>
            <div
              style={{ display: "none" }}
              onClick={() => {
                setCanAdd(true)
              }}
            >
              Add
            </div>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              paddingBottom: "8px",
              marginBottom: "8px",
              borderBottom: "1px solid #dddddd",
            }}
          >
            <div
              onClick={() => {
                setHighlights(demo_1)
                setUrl("/demo_1.pdf")
              }}
              style={{
                flex: 1,
                backgroundColor: "#007bff",
                borderColor: "#007bff",
                color: "white",
                padding: "8px",
                textAlign: "center",
                borderRadius: "4px",
                cursor: "pointer",
                marginRight: "4px",
              }}
            >
              Demo 1
            </div>
            <div
              onClick={() => {
                setHighlights(demo_2)
                setUrl("/demo_2.pdf")
              }}
              style={{
                flex: 1,
                backgroundColor: "#007bff",
                borderColor: "#007bff",
                color: "white",
                padding: "8px",
                textAlign: "center",
                borderRadius: "4px",
                cursor: "pointer",
              }}
            >
              Demo 2
            </div>
          </div>
          <div>
            {Array.isArray(highlights) &&
              highlights.map((v, i) => {
                let _data = v?.data || {}
                let _colorPrimary =
                  _data.isCorrect === true
                    ? _colorCorrect
                    : _data.isCorrect === false
                    ? _colorIncorrect
                    : _colorNull
                let _colorPrimaryLight =
                  _data.isCorrect === true
                    ? _colorCorrectLight
                    : _data.isCorrect === false
                    ? _colorIncorrectLight
                    : _colorNullLight
                return (
                  <div key={i}>
                    <div
                      style={{
                        background: _colorPrimaryLight,
                        padding: "8px",
                        marginBottom: "8px",
                        border: `1px dotted ${_colorPrimary}`,
                        borderRadius: "4px",
                        cursor: "pointer",
                      }}
                      onClick={() => {
                        document.location.hash = `highlight-${v.id}`
                      }}
                    >
                      <div style={{ fontWeight: "bold", marginBottom: "4px" }}>
                        {_data.fieldName || _data.label}
                      </div>
                      <div
                        style={{
                          border: "1px solid #ffd372",
                          padding: "4px",
                          background: "#ffefcc",
                          borderRadius: "4px",
                          marginBottom: "4px",
                          wordBreak: "break-word",
                        }}
                      >
                        {_data.value || "No data"}
                      </div>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          alignItems: "center",
                        }}
                      >
                        <button
                          style={{
                            background: "green",
                            borderRadius: "4px",
                            color: "white",
                            marginRight: "4px",
                            outline: "none",
                            border: 0,
                            padding: "4px",
                            cursor: "pointer",
                          }}
                          onClick={ev => {
                            _data.isCorrect = true
                            if (_data._forceUpdate) {
                              _data._forceUpdate()
                            }
                            setC(new Date().getTime())
                            ev.stopPropagation()
                            ev.preventDefault()
                          }}
                        >
                          Verified
                        </button>
                        <button
                          style={{
                            background: "red",
                            borderRadius: "4px",
                            color: "white",
                            marginRight: "4px",
                            outline: "none",
                            border: 0,
                            padding: "4px",
                            cursor: "pointer",
                          }}
                          onClick={ev => {
                            _data.isCorrect = false
                            if (_data._forceUpdate) {
                              _data._forceUpdate()
                            }
                            setC(new Date().getTime())
                            ev.stopPropagation()
                            ev.preventDefault()
                          }}
                        >
                          Reject
                        </button>
                      </div>
                    </div>
                  </div>
                )
              })}
          </div>
          <div>
            <div>
              <input
                id="cbshowhighlight"
                type="checkbox"
                checked={showH}
                onChange={ev => {
                  setShowH(ev.currentTarget.checked)
                }}
              />
              <label htmlFor="cbshowhighlight">Show highlights</label>
            </div>
          </div>
        </div>

        {/* PDF Viewer */}
        <div
          style={{
            flex: 1,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            overflow: "auto",
            backgroundColor: "#eaeaea",
          }}
        >
          <UI
            config={{
              url: url,
              // url: "https://arxiv.org/pdf/1708.08021",
              highlights: highlights,
              showHighlight: showH,
              canAddHighlight: canAdd,
              modeAddHighlight: "area",
              refineAddHighlight: data => {
                console.log("refineAddHighlight:", data)
                let _data = {
                  isArea: data.content & data.content.text ? false : true,
                  label: data?.comment?.text,
                  isCorrect: null,
                  value:
                    data.content & data.content.text ? data.content.text : null,
                }
                return {
                  position: data.position,
                  data: _data,
                }
              },
              onAddHighlight: highlight => {
                setHighlights(prevHighlights => [
                  {
                    ...highlight,
                    id: highlight.id || String(Math.random()).slice(2),
                  },
                  ...prevHighlights,
                ])
              },
              // renderUIAddHighlight: () => {
              //   return <div>Label</div>
              // },
            }}
          />
        </div>
      </div>
    </div>
  )
}

