import React, { useState, useEffect, useCallback, useRef } from "react"
import * as pdfjsLib from "pdfjs-dist/build/pdf.mjs"
import {
  AreaHighlight,
  Highlight,
  PdfHighlighter,
  PdfLoader,
  Popup,
  Tip,
} from "./libs"
import type { Content, IHighlight, NewHighlight, ScaledPosition } from "./libs"

import { Sidebar } from "./UISidebar"
import { Spinner } from "./UISpinner"

window["pdfjsLib"] = pdfjsLib
const getNextId = () => String(Math.random()).slice(2)

const parseIdFromHash = () => document.location.hash.slice("#highlight-".length)

const resetHash = () => {
  document.location.hash = ""
}

const HighlightPopup = ({
  comment,
}: {
  comment: { text: string; emoji: string }
}) =>
  comment.text ? (
    <div className="Highlight__popup">
      {comment.emoji} {comment.text}
    </div>
  ) : null

export default function App(props: any = {}) {
  const config = props.config || {}
  const showSidebar = config.showSidebar === true ? true : false
  const showPopupHighlight = config.showPopupHighlight === true ? true : false
  const modeAddHighlight = config.modeAddHighlight
    ? config.modeAddHighlight
    : "text"
  const canMoveAreaHighlight = config.showPopupHighlight === true ? true : false
  const refineAddHighlight = config.refineAddHighlight || null
  const onAddHighlight = config.onAddHighlight
  const searchParams = new URLSearchParams(document.location.search)

  const [url, setUrl] = useState(config.url)
  const [canAddHighlight, setCanAddHighlight] = useState(
    config.canAddHighlight === true ? true : false
  )
  const [showHighlight, setShowHighlight] = useState(
    config.showHighlight === false ? false : true
  )
  const [highlights, setHighlights] = useState<Array<IHighlight>>(
    Array.isArray(config.highlights) ? config.highlights : []
  )

  useEffect(() => {
    if (url != config.url) {
      setUrl(config.url)
    }
  }, [config.url])
  useEffect(() => {
    if (canAddHighlight != config.canAddHighlight) {
      setCanAddHighlight(config.canAddHighlight)
    }
  }, [config.canAddHighlight])
  useEffect(() => {
    if (showHighlight != config.showHighlight) {
      setShowHighlight(config.showHighlight)
    }
  }, [config.showHighlight])
  useEffect(() => {
    console.log("change highlights:", config.highlights)
    setHighlights(config.highlights)
  }, [config.highlights])

  const resetHighlights = () => {
    setHighlights([])
  }

  const toggleDocument = () => {}

  const scrollViewerTo = useRef((highlight: IHighlight) => {})

  const scrollToHighlightFromHash = useCallback(() => {
    const highlight = getHighlightById(parseIdFromHash())
    if (highlight) {
      scrollViewerTo.current(highlight)
    }
  }, [])

  useEffect(() => {
    window.addEventListener("hashchange", scrollToHighlightFromHash, false)
    return () => {
      window.removeEventListener("hashchange", scrollToHighlightFromHash, false)
    }
  }, [scrollToHighlightFromHash])

  const getHighlightById = (id: string) => {
    return highlights.find(highlight => highlight.id === id)
  }

  const addHighlight = (highlight: NewHighlight) => {
    console.log("Saving highlight", highlight)
    if (onAddHighlight) {
      onAddHighlight(highlight)
    } else {
      setHighlights(prevHighlights => [
        { ...highlight, id: highlight.id || getNextId() },
        ...prevHighlights,
      ])
    }
  }

  const updateHighlight = (
    highlightId: string,
    position: Partial<ScaledPosition>,
    content: Partial<Content>
  ) => {
    console.log("Updating highlight", highlightId, position, content)
    setHighlights(prevHighlights =>
      prevHighlights.map(h => {
        const {
          id,
          position: originalPosition,
          content: originalContent,
          ...rest
        } = h
        return id === highlightId
          ? {
              id,
              position: { ...originalPosition, ...position },
              content: { ...originalContent, ...content },
              ...rest,
            }
          : h
      })
    )
  }

  if (url == null) {
    if (config.renderNoURL) {
      return config.renderNoURL()
    }
    return <div>No url</div>
  }

  console.log("render UI2:", showHighlight)
  return (
    <div
      className="pdfhl-container"
      style={{ display: "flex", height: "100%", width: "100%" }}
    >
      {showSidebar && (
        <Sidebar
          highlights={highlights}
          resetHighlights={resetHighlights}
          toggleDocument={toggleDocument}
        />
      )}
      <div
        className="pdfhl-viewer"
        style={{
          height: "100%",
          width: "100%",
          position: "relative",
        }}
      >
        <PdfLoader
          url={url}
          workerSrc={config.workerSrc}
          beforeLoad={<Spinner />}
        >
          {pdfDocument => (
            <PdfHighlighter
              pdfDocument={pdfDocument}
              canAddHighlight={canAddHighlight}
              showHighlight={showHighlight}
              enableAreaSelection={event => {
                if (modeAddHighlight == "text" && event.altKey) {
                  return true
                }
                if (modeAddHighlight == "area") {
                  return true
                }
                return false
              }}
              onScrollChange={resetHash}
              scrollRef={scrollTo => {
                scrollViewerTo.current = scrollTo
                scrollToHighlightFromHash()
              }}
              onSelectionFinished={(
                position,
                content,
                hideTipAndSelection,
                transformSelection
              ) => (
                <Tip
                  onOpen={transformSelection}
                  renderUI={config.renderUIAddHighlight}
                  onConfirm={comment => {
                    let _highlight = refineAddHighlight
                      ? refineAddHighlight({ content, position, comment })
                      : { content, position, comment }
                    addHighlight(_highlight)
                    hideTipAndSelection()
                  }}
                />
              )}
              highlightTransform={(
                highlight,
                index,
                setTip,
                hideTip,
                viewportToScaled,
                screenshot,
                isScrolledTo
              ) => {
                // if (showHighlight === false) {
                //   return <div key={index}></div>
                // }
                const isTextHighlight =
                  (highlight as any)?.data?.isArea === true ? false : true
                // console.log("isTextHighlight:", isTextHighlight)

                const component = isTextHighlight ? (
                  <Highlight
                    isScrolledTo={isScrolledTo}
                    highlight={highlight}
                  />
                ) : (
                  <AreaHighlight
                    isScrolledTo={isScrolledTo}
                    highlight={highlight}
                    canMoveAreaHighlight={canMoveAreaHighlight}
                    onChange={boundingRect => {
                      updateHighlight(
                        highlight.id,
                        { boundingRect: viewportToScaled(boundingRect) },
                        { image: screenshot(boundingRect) }
                      )
                    }}
                  />
                )

                return (
                  <Popup
                    popupContent={<HighlightPopup {...highlight} />}
                    onMouseOver={popupContent => {
                      if (showPopupHighlight) {
                        setTip(highlight, highlight => popupContent)
                      }
                    }}
                    onMouseOut={hideTip}
                    key={index}
                  >
                    {component}
                  </Popup>
                )
              }}
              highlights={highlights}
            />
          )}
        </PdfLoader>
      </div>
    </div>
  )
}
