import { Rnd } from "react-rnd"
import { getPageFromElement } from "../lib/pdfjs-dom"
import styles from "../style/AreaHighlight.module.css"
import stylesHighlight from "../style/Highlight.module.css"
import type { LTWHP, ViewportHighlight } from "../types"
import {
  getStyleByCorrect,
  getStyleLabelByCorrect,
} from "../lib/optimize-client-rects"
import { useState } from "react"

interface Props {
  highlight: ViewportHighlight
  onChange: (rect: LTWHP) => void
  isScrolledTo: boolean
  canMoveAreaHighlight?: boolean
}

export function AreaHighlight({
  highlight,
  onChange,
  isScrolledTo,
  canMoveAreaHighlight,
  ...otherProps
}: Props) {
  const [_, setState] = useState({})
  const forceUpdate = () => setState({})
  const item: any = highlight ?? {}
  if (item.data) {
    item.data._forceUpdate = forceUpdate
  }

  if (!item.position) {
    return null
  }
  const data = item.data || {}
  let _styleByCorrect = getStyleByCorrect(data)
  let _styleLabelByCorrect = getStyleLabelByCorrect(data)
  return (
    <div
      className={`${styles.areaHighlight} ${
        isScrolledTo ? styles.scrolledTo : ""
      }`}
    >
      <Rnd
        disableDragging={canMoveAreaHighlight === true ? false : true}
        enableResizing={canMoveAreaHighlight === true ? true : false}
        className={styles.part}
        onDragStop={(_, data) => {
          const boundingRect: LTWHP = {
            ...highlight.position.boundingRect,
            top: data.y,
            left: data.x,
          }
          onChange(boundingRect)
        }}
        onResizeStop={(_mouseEvent, _direction, ref, _delta, position) => {
          const boundingRect: LTWHP = {
            top: position.y,
            left: position.x,
            width: ref.offsetWidth,
            height: ref.offsetHeight,
            pageNumber: getPageFromElement(ref)?.number || -1,
          }
          onChange(boundingRect)
        }}
        position={{
          x: highlight.position.boundingRect.left,
          y: highlight.position.boundingRect.top,
        }}
        size={{
          width: highlight.position.boundingRect.width,
          height: highlight.position.boundingRect.height,
        }}
        onClick={(event: React.MouseEvent) => {
          event.stopPropagation()
          event.preventDefault()
        }}
        style={{
          ..._styleByCorrect,
          ...data.style,
        }}
        {...otherProps}
      >
        {data.label && (
          <div className={`${stylesHighlight.pdfhlWrapLabel}`}>
            <div
              className={`${stylesHighlight.pdfhlLabel}`}
              style={{ ..._styleLabelByCorrect }}
            >
              {data.label}
            </div>
          </div>
        )}
      </Rnd>
    </div>
  )
}
