import React from "react"
// import MUI_sample_nolazy from "./sample_nolazy"
import MUI_UI from "../UI"
// const MUI_sample = React.lazy(() => import("./sample"))

const List = {
  // bigcalendar_sample: MUI_sample,
}

const ListNoLazy = {
  // bigcalendar_sample_nolazy: MUI_sample_nolazy,
  pdfhighlight_ui: MUI_UI,
}
const MyUIOne = {
  init() {
    console.log("pdfhighlight init")
    if (window && window.MLib && window.MLib.MyUIHelpers) {
      if (window.MLib.MyUIHelpers.addTypesFromListLazy) {
        window.MLib.MyUIHelpers.addTypesFromListLazy(List)
      }
      if (window.MLib.MyUIHelpers.addTypesFromListWithoutLazy) {
        window.MLib.MyUIHelpers.addTypesFromListWithoutLazy(ListNoLazy)
      }
    }
  },
}

export default MyUIOne
