import React from "react"
import "./index.css"
import App from "./App"
import MyUIOne from "./MyUI"

import { createRoot } from "react-dom/client"
// After react18
const Micro = { root: null }
window[`render-pdfhighlight`] = (
  containerId,
  history,
  onDidMount,
  children,
  moreProps = {}
) => {
  if (document.getElementById(containerId)) {
    Micro.root = createRoot(document.getElementById(containerId))
    Micro.root.render(
      <App
        history={history}
        onDidMount={onDidMount}
        children={children}
        {...moreProps}
      />
    )
  }
}

window[`unmount-pdfhighlight`] = containerId => {
  if (document.getElementById(containerId) && Micro.root) {
    Micro.root.unmount()
  }
}

if (!document.getElementById(`pdfhighlight-container`)) {
  Micro.root = createRoot(document.getElementById("root"))
  Micro.root.render(<App />)
}

MyUIOne.init()
