import { useState } from "react"
import {
  getStyleByCorrect,
  getStyleLabelByCorrect,
} from "../lib/optimize-client-rects"
import styles from "../style/Highlight.module.css"
// import type { LTWHP } from "../types.js"

interface Props {
  // position: {
  //   boundingRect: LTWHP
  //   rects: Array<LTWHP>
  // }
  onClick?: () => void
  onMouseOver?: () => void
  onMouseOut?: () => void
  highlight?: any
  isScrolledTo: boolean
}

export function Highlight({
  onClick,
  onMouseOver,
  onMouseOut,
  highlight,
  isScrolledTo,
}: Props) {
  const [_, setState] = useState({})
  const forceUpdate = () => setState({})
  const item = highlight ?? {}
  if (item.data) {
    item.data._forceUpdate = forceUpdate
  }
  if (!item.position) {
    return null
  }
  const { rects, boundingRect } = item.position
  const data = item.data
  let _styleByCorrect = getStyleByCorrect(data)
  let _styleLabelByCorrect = getStyleLabelByCorrect(data)
  return (
    <div
      className={`Highlight ${styles.highlight} ${
        isScrolledTo ? styles.scrolledTo : ""
      }`}
    >
      {data ? (
        <div
          className={`Highlight__emoji ${styles.emoji}`}
          style={{
            left: 20,
            top: boundingRect.top,
          }}
        >
          {data.emoji}
        </div>
      ) : null}
      <div className={`Highlight__parts ${styles.parts}`}>
        {rects.map((rect, index) => (
          <div
            onMouseOver={onMouseOver}
            onMouseOut={onMouseOut}
            onClick={onClick}
            // biome-ignore lint/suspicious/noArrayIndexKey: We can use position hash at some point in future
            key={index}
            style={{
              ...rect,
              ..._styleByCorrect,
              ...data.style,
            }}
            className={`Highlight__part ${styles.part}`}
          >
            {data.label && (
              <div className={`${styles.pdfhlWrapLabel}`}>
                <div
                  className={`${styles.pdfhlLabel}`}
                  style={{ ..._styleLabelByCorrect }}
                >
                  {data.label}
                </div>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  )
}
